$KEYCODE_UP: "ArrowUp";
$KEYCODE_DOWN: "ArrowDown";
$KEYCODE_LEFT: "ArrowLeft";
$KEYCODE_RIGHT: "ArrowRight";
$KEYCODE_UP_LEFT: "KeyQ";
$KEYCODE_UP_RIGHT: "KeyP";
$KEYCODE_DOWN_LEFT: "KeyZ";
$KEYCODE_DOWN_RIGHT: "KeyM";

.cm-piece {
  position: absolute;
  top: 0;
  left: 0;
  width: 110px;
  height: 110px;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  transition: top 0.3s, left 0.3s, transform 0.5s;
  &:before {
    content: "";
    width: 70%;
    height: 70%;
    display: inline-block;
    background-repeat: none;
    background-size: 100% 100%;
  }
  &.is-raised {
    z-index: 101;
    transform: translateY(-2.8rem);
    transition: top 0.3s, left 0.3s, transform 0.5s;
  }
  &.did-fall {
    transform: scale(0.1);
    transform-origin: bottom;
    opacity: 0;
    transition: opacity 0.8s 0.2s ease-in, transform 1s ease-in;
  }
  &.has-sword {
    &:after {
      content: "";
      position: absolute;
      right: 9%;
      bottom: 12%;
      width: 30%;
      height: 30%;
      background-repeat: none;
      background-size: 100% 100%;
      background-image: url("./assets/images/action-sword.svg");
    }
  }
  &.has-shield {
    &:after {
      content: "";
      position: absolute;
      right: 6%;
      bottom: 5%;
      width: 27%;
      height: 27%;
      background-repeat: none;
      background-size: 100% 100%;
      background-image: url("./assets/images/action-shield.svg");
    }
  }
  .cm-board-width-3 & {
    width: calc((100%) / 3);
    height: calc((100%) / 3);
  }
  .cm-board-width-4 & {
    width: calc((100%) / 4);
    height: calc((100%) / 4);
  }
  .cm-board-width-5 & {
    width: calc((100%) / 5);
    height: calc((100%) / 5);
    @media screen and (max-width: 720px) {
      &:before {
        width: 80%;
        height: 80%;
      }
    }
  }
  .cm-board-width-6 & {
    width: calc((100%) / 6);
    height: calc((100%) / 6);
    @media screen and (max-width: 720px) {
      &:before {
        width: 80%;
        height: 80%;
      }
    }
  }
  .cm-board-width-7 & {
    width: calc((100%) / 7);
    height: calc((100%) / 7);
    @media screen and (max-width: 720px) {
      &:before {
        width: 80%;
        height: 80%;
      }
    }
  }
}
.cm-piece-direction {
  &:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    color: #ac49ff;
  }
  &.cm-piece-direction--#{$KEYCODE_UP} {
    &:after {
      top: 0;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-bottom: 11px solid;
    }
  }
  &.cm-piece-direction--#{$KEYCODE_UP_LEFT} {
    &:after {
      top: -1px;
      left: -7px;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-bottom: 11px solid;
      transform: rotate(-45deg);
    }
  }
  &.cm-piece-direction--#{$KEYCODE_UP_RIGHT} {
    &:after {
      top: -1px;
      right: -7px;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-bottom: 11px solid;
      transform: rotate(45deg);
    }
  }
  &.cm-piece-direction--#{$KEYCODE_DOWN} {
    &:after {
      bottom: 0;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-top: 11px solid;
    }
  }
  &.cm-piece-direction--#{$KEYCODE_DOWN_LEFT} {
    &:after {
      bottom: -1px;
      left: -7px;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-top: 11px solid;
      transform: rotate(45deg);
    }
  }
  &.cm-piece-direction--#{$KEYCODE_DOWN_RIGHT} {
    &:after {
      bottom: -1px;
      right: -7px;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-top: 11px solid;
      transform: rotate(-45deg);
    }
  }
  &.cm-piece-direction--#{$KEYCODE_LEFT} {
    &:after {
      top: calc(50% - 11px);
      left: 0;
      border-top: 11px solid transparent;
      border-bottom: 11px solid transparent;
      border-right: 11px solid;
    }
  }
  &.cm-piece-direction--#{$KEYCODE_RIGHT} {
    &:after {
      top: calc(50% - 11px);
      right: 0;
      border-top: 11px solid transparent;
      border-bottom: 11px solid transparent;
      border-left: 11px solid;
    }
  }
  &.is-raised:after {
    display: none;
  }
}
.cm-piece--king:before {
  background-image: url("./assets/images/king-white.svg");
}
.cm-piece--queen:before {
  background-image: url("./assets/images/queen-black.svg");
}
.cm-piece--rook:before {
  background-image: url("./assets/images/rook-black.svg");
}
.cm-piece--bishop:before {
  background-image: url("./assets/images/bishop-black.svg");
}
