:root {
  --max-board-width: 620px;
  --max-square-width: 110px;
}

.cm-board-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  align-items: center;
  @media screen and (min-width: 720px) {
    justify-content: center;
  }
}
.cm-info-panel {
  padding: 0 2rem 2.5rem;
  max-width: 550px;
  @media screen and (min-width: 720px) {
    padding-bottom: 4rem;
  }
}
.cm-info-panel__lives-counter {
  display: inline-block;
  font-size: 2rem;
  color: rgb(254, 73, 73);
  font-weight: bold;
  &:before {
    content: "‚ù§Ô∏è";
  }
}
.cm-info-panel__message {
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  font-weight: bold;
  text-align: left;
  &:before {
    content: "üí¨";
    margin-right: 1rem;
    font-size: 2.5rem;
  }
  &:empty {
    &:before {
      content: "";
    }
  }
  @media screen and (min-width: 720px) {
    font-size: 1.8rem;
  }
}
.cm-castle {
  position: relative;
  height: 100vw;
  width: 100vw;
  max-width: var(--max-board-width);
  max-height: var(--max-board-width);
  margin-bottom: 40px;
  border: 8px solid #757575;
  border-radius: 6px;
  &.cm-board-width-3 {
    max-width: calc(var(--max-square-width) * 3);
    max-height: calc(var(--max-square-width) * 3);
  }
  &.cm-board-width-4 {
    max-width: calc(var(--max-square-width) * 4);
    max-height: calc(var(--max-square-width) * 4);
  }
  &.cm-board-width-5 {
    max-width: calc(var(--max-square-width) * 5);
    max-height: calc(var(--max-square-width) * 5);
  }
  &.cm-board-width-6 {
    max-width: calc(var(--max-square-width) * 6);
    max-height: calc(var(--max-square-width) * 6);
  }
  &.cm-board-width-7 {
    max-width: calc(var(--max-square-width) * 7);
    max-height: calc(var(--max-square-width) * 7);
  }
}
.cm-board {
  position: relative;
  height: 100%;
  width: 100%;
  font-family: "Courier New", monospace;
}
.cm-board__inner {
  height: 100%;
  display: grid;
  background-color: #242424;
}
@keyframes hit-animation {
  0%,
  25%,
  50%,
  75%,
  100% {
    opacity: 1;
  }
  12%,
  37%,
  62%,
  87% {
    opacity: 0;
  }
}
.cm-board__row {
  display: flex;
  &:nth-child(odd) {
    .cm-board__square:nth-child(even) {
      background-color: #88a96c;
      color: #eeefd1;
      border-color: #484848;
      &.is-raised {
        &:after {
          background: #5f774b;
        }
      }
    }
  }
  &:nth-child(even) {
    .cm-board__square:nth-child(odd) {
      background-color: #88a96c;
      color: #eeefd1;
      border-color: #484848;
      &.is-raised {
        &:after {
          background: #5f774b;
        }
      }
    }
  }
  &:last-child {
    .cm-board__square:not(.is-raised):after {
      display: none;
    }
  }
}
.cm-board__square {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: #f1f2e3;
  background-size: 70% 70%;
  background-repeat: no-repeat;
  background-position: center;
  color: #769555;
  border: 1px solid #606060;
  border-radius: 3px;
  cursor: pointer;
  transform: translateY(0);
  transition: transform 0.5s, filter 0.5s, height 0.5s;
  &.is-missing {
    opacity: 0;
  }
  &.is-target:not(.is-raised):before,
  &.is-hit-animation:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #7f00ff !important;
    opacity: 0.5;
  }
  &.is-hit-animation:before {
    animation: hit-animation 2s forwards;
  }
  &.cm-action--exit {
    background-image: url("./assets/images/action-exit.svg");
  }
  &.cm-action--exit--closed {
    background-image: url("./assets/images/action-exit--closed.svg");
  }
  &.cm-action--key {
    background-image: url("./assets/images/action-key.svg");
  }
  &.cm-action--lever-off {
    background-image: url("./assets/images/action-lever-off.svg");
  }
  &.cm-action--lever-on {
    background-image: url("./assets/images/action-lever-on.svg");
  }
  &.cm-action--stairs {
    background-image: url("./assets/images/action-stairs.svg");
  }
  &.cm-action--sword {
    background-image: url("./assets/images/action-sword.svg");
  }
  &.cm-action--shield {
    background-image: url("./assets/images/action-shield.svg");
  }
  &.is-raised {
    height: 86%;
    z-index: 100;
    transform: translateY(-16%);
    filter: brightness(0.6);
    transition: transform 0.5s, filter 0.5s, height 0.5s;
  }
  &:after {
    content: "";
    width: 100%;
    height: 32%;
    background: #919090;
    position: absolute;
    bottom: -33%;
  }
}
